









































import {mapState, mapMutations} from 'vuex';
export default {
	
	data() {
		return {
			phoneNumber: '',
			password: '',
			showcansee: false,
			focus:0
		};
	},
	computed: {
		correctPhone() {
			return this.$utils.checkMobile(this.phoneNumber);
		},
		imageurl() {
			return this.showcansee ? '../../../static/img/viewicon.png' : '../../../static/img/viewnoicon.png'
		},
		inputType(){
			return this.showcansee ?'text':'password'
		}
		
	},
	methods:{
		...mapMutations(['login']),
		userFocus:function(){
			this.focus = 0;
		},
		passFocus:function(){
			this.focus = 1;
		},
		seePass: function() {
			this.showcansee = !this.showcansee
		},
		
		getPhoneNumber: function(e) {
			this.phoneNumber = e.target.value;
			if(this.correctPhone){
				this.piccode = this.$imageUrl+'verify';
			}
		},
		getPassWord: function(e) {
			this.password = e.target.value;
		},
		messagelogin:function(){
			uni.navigateTo({
				url: '../quckLogin/quckLogin',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		findpass:function(){
			uni.navigateTo({
				url: '../findPass/findPass',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		register:function(){
			uni.navigateTo({
				url: '../register/register',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		login:function(){
			if (!this.correctPhone) {
				uni.showToast({
					title: '请输入正确手机号',
					mask: false,
					duration: 1500,
					icon:'none'
				}); 
				return;
			}
			if (this.password == '') {
				uni.showToast({
					title: '请输入密码',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			this.$utils.tRequest({
				url: this.$webUrl+'user_login',
				method: 'GET',
				data: {
					username:this.phoneNumber,
					password:this.password
				},
				success: res => {
					if(res.code == 0){
						this.$store.commit("login",res.data)
						uni.switchTab({
							url:"../../index/index"
						}) 
					}else{
						uni.showToast({
							title: res.msg,
							mask: false,
							duration: 1500,
							icon:'none',
						});
					}
				},
				fail: (res) => {
					console.log(JSON.stringify(res))
				},
				complete: (res) => {}
			})
			
		}
		
	}
}
